    debug(`Using parameters: ${parameters
        .map((p) => `${p.ParameterKey}: ${p.ParameterValue}`)
        .join(', ')}`);
    let update = false;
    if (hasExistingStack) {
        const shouldDelete = await shouldDeleteExistingStack(client, cfStackName);
        if (shouldDelete) {
            warning(`Deleting existing stack ${cfStackName}, due to ${StackStatus.ROLLBACK_COMPLETE} status`);
            await deleteExistingStack(client, cfStackName);
        }
        else {
            update = true;
        }
    }
    return update;
